import React from "react"
import { graphql, StaticQuery } from "gatsby"
import CookieConsent from "react-cookie-consent"
import { renderRichText } from "gatsby-source-contentful/rich-text"

import Modal from "../modal"

const Privacity = () => {
    return (
        <StaticQuery
            query={graphql`
                query {
                    contentfulPaginas(
                        contentful_id: { eq: "5O4lhsnjbWPt1TC5l9TIu0" }
                    ) {
                        privacidade {
                            raw
                        }
                    }
                }
            `}
            render={data => (
                <CookieConsent
                    cookieName="gatsby-gdpr-google-analytics"
                    style={{ backgroundColor: "#000000" }}
                    location="bottom"
                    declineButtonText="Rejeitar"
                    buttonText="Aceitar"
                    buttonStyle={{
                        backgroundColor: "#ffffff",
                        color: "#0f0f0f",
                    }}
                >
                    Utilizamos cookies para melhorar sua experincia de
                    navegao. Ao utilizar nossos servios, voc concorda com
                    nossa
                    <Modal
                        button="Poltica de Privacidade"
                        title="Poltica de Privacidade"
                    >
                        {renderRichText(data.contentfulPaginas.privacidade)}
                    </Modal>
                </CookieConsent>
            )}
        />
    )
}

export default Privacity
